<template>
  <div id="group-discussion">
    <head-top :title="groupName">
      <router-link to="/teamRank" class="head-right">
        <i class="iconfont icon-chengyuan"></i>
      </router-link>
    </head-top>

    <!-- 头部摘要 -->
    <div class="summary">
      <div>
        论题:
        <span>{{ total }}</span>
      </div>
      <div>
        得分数:
        <span>{{ score }}</span>
      </div>
      <div>
        点赞数:
        <span>{{ good }}</span>
      </div>
    </div>

    <!-- 聊天信息 -->
    <div class="chat" ref="chat">
      <div class="chat_title" v-if="discussionSubject">
        <span class="chat_title_left">[论题]</span>
        <span
          v-if="discussionSubject['GSForm'] === contentTypeDefault">{{ discussionSubject["GSContent"] }}</span>
        <img v-else alt="" :src="getDiscussionImage(discussionSubject['GSContent'])"
          :style="{ maxWidth: '70vw', maxHeight: '15rem' }"
          @click="preview(getDiscussionImage(discussionSubject['GSContent']))" />
      </div>

      <div v-show="discussionContents && discussionContents.length > 0" class="chat_content">
        <div v-for="(sub, k) in discussionContents" :key="k"
          :class="sub['StudentID'] !== $store.getters.studentID ? 'chat_item left_item' : 'chat_item right_item'">
          <div class="chat_icon">
            <!-- {{sub['StudentPhoto']}} -->
            <img :src="defaultPhotoImg" alt="" />
          </div>

          <div class="chat_news">
            <div class="chat_news_name">{{ sub["StudentName"] }}</div>
            <div class="chat_news_cont">
              <span v-if="sub['GDForm'] === contentTypeDefault">{{ sub["GDContent"] }}</span>
              <img v-else :src="getImageFile(sub['GDContent'])" alt
                @click="preview(getImageFile(sub['GDContent']))" />
              <div class="chat_news_arrow"></div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 输入框 -->
    <div class="footer" v-show="discussionSubject">
      <div class="footer_text">
        <input type="text" v-model="content" :disabled="!discussionSubject" placeholder="请输入讨论内容"
          @keyup.enter="dataAdd" />
      </div>
      <div class="icons" v-if="content.length === 0">
        <i class="iconfont icon-tupian" :disabled="!discussionSubject" @click="selectPhoto(1)"></i>
        <i class="iconfont icon-photograph" :disabled="!discussionSubject"
          @click="selectPhoto(2)"></i>
      </div>
      <div class="icons" v-else>
        <van-button size="small" type="primary" @click="dataAdd">发送</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import RImage from "@/components/r-image";
import { ImagePreview } from "vant";
import defaultPhoto from "@/assets/boy_img.png";

import { ContentType, ModulePicture, DiscussFunc } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  name: "",
  components: { RImage },
  props: {},
  data() {
    return {
      // score: 0,
      total: 0,
      // good: 0,
      contentType: ContentType.Text, //1文本 2图片
      contentTypeDefault: ContentType.Text,
      content: "",
      imgBase: "",

      defaultPhotoImg: defaultPhoto,
      groupName: this.$store.getters.group
        ? this.$store.getters.group.groupName || "暂无组名"
        : "暂无组名",
      showEdit: false,
      imagePreview: null,

      discussionSubject: null,
      discussionContents: []
    };
  },
  computed: {
    score() {
      let score = 0;
      this.discussionContents.forEach(r => {
        score += r["GDScore"];
      });
      return score;
    },
    good() {
      let good = 0;
      this.discussionContents.forEach(r => {
        good += r["GDGood"] ? 1 : 0;
      });
      return good;
    }
  },
  created() {
    this.getDataStatics();
    this.getDataDetail();
  },
  mounted() {
    this.$nextTick(() => {
      window.uploadImage = imgFile => {
        this.afterRead(imgFile);
      };
    });
  },
  methods: {
    preview(image) {
      this.imagePreview = ImagePreview({
        images: [image],
        showIndex: false,
        onClose() {}
      });
    },
    getDataStatics() {
      this.$gatewayHttp.groupDiscussion
        .getStatistic({
          groupID: this.$store.getters.groupID
        })
        .then(res => {
          if (res.Result) {
            // this.score = res.Score || 0;
            this.total = res.Total || 0;
            // this.good = res.Good || 0;
          }
        })
        .catch(err => {});
    },
    getDataDetail() {
      // this.dataList = [];
      this.$gatewayHttp.groupDiscussion
        .getList({
          groupID: this.$store.getters.groupID
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            this.discussionSubject = res.Datas[0]["Key"];

            res.Datas[0]["Value"].sort((a, b) => {
              if (a.SubmitTime < b.SubmitTime) {
                return -1;
              } else {
                return 1;
              }
            });
            res.Datas[0]["Value"].forEach(sub => {
              let subIndex = this.discussionContents.findIndex(r => {
                return r["GDID"] === sub["GDID"];
                // return JSON.stringify(r) === JSON.stringify(sub);
              });
              if (subIndex === -1) {
                this.discussionContents.push(sub);
                this.$nextTick(() => {
                  console.log(
                    this.$refs.chat.scrollHeight,
                    this.$refs["chat"].scrollTop
                  );
                  this.$refs["chat"].scrollTop = this.$refs.chat.scrollHeight;
                  setTimeout(() => {
                    this.$refs["chat"].scrollTop = this.$refs.chat.scrollHeight;
                  }, 1000);
                });
              } else {
                this.discussionContents.splice(subIndex, 1, sub);
              }
            });
          }
        })
        .catch(err => {});
    },
    dataAdd() {
      if (!this.content) {
        return;
      }
      this.$gatewayHttp.groupDiscussion
        .postDataSave({
          gsid: this.discussionSubject["GSID"],
          studentID: this.$store.getters.studentID,
          groupID: this.$store.getters.groupID, // "19062313411770094101179090842305" ||
          gdForm: this.contentType,
          gdContent: this.content
        })
        .then(res => {
          if (res.Result) {
            this.content = "";
            this.contentType = this.contentTypeDefault;
          } else if (res.Info) {
            this.$toast(res.Info);
          }
        })
        .catch(err => {});
    },
    getDiscussionImage(fileId) {
      return this.$gatewayHttp.resourse.picture(
        fileId,
        ModulePicture.GroupSubject
      );
    },
    getImageFile(fileId) {
      return this.$gatewayHttp.resourse.picture(
        fileId,
        ModulePicture.GroupDiscuss
      );
    },
    selectPhoto(type) {
      if (!this.discussionSubject) return;
      if (type === 2) {
        sendToAPP(FuncType.cameraOpen, "");
      } else {
        sendToAPP(FuncType.albumOpen, "");
      }
    },
    afterRead(file) {
      console.log("接收到的base64：", file);
      // if (
      //   file.type !== "image/jpeg" &&
      //   file.type !== "image/png" &&
      //   file.type !== "image/jpg"
      // ) {
      //   this.$toast("请上传 jpg 格式图片");
      //   return false;
      // }
      // 超过20M
      if (file.length > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      if (file) {
        let reqData = {
          funType: ModulePicture.GroupDiscuss,
          imgBaseString: file
        };
        this.$gatewayHttp.resourse
          .upload(reqData)
          .then(res => {
            if (res.Result) {
              this.contentType = ContentType.Picture;
              this.imgBase = file;
              this.content = res.Data;
              this.dataAdd();
            }
          })
          .catch(err => {});
      }
    }
  },
  watch: {
    "$store.getters.groupDiscussion"(newVal, oldVal) {
      if (newVal !== oldVal) {
        if (newVal.type === DiscussFunc.init) {
          this.getDataStatics();
          this.getDataDetail();
          this.discussionSubject = null;
          this.discussionContents = [];
        } else if (newVal.type === DiscussFunc.newMsg) {
          this.getDataDetail();
        } else if (newVal.type === DiscussFunc.good) {
          // this.good += newVal.value["IsGood"] ? 1 : -1;
          this.getDataDetail();
        } else if (newVal.type === DiscussFunc.score) {
          // this.score += newVal.value["IsAdd"] ? 1 : -1;
          this.getDataDetail();
        }
      }
    },
    discussionContents(newVal, oldVal) {
      // setTimeout(() => {
      if (newVal.length === oldVal.length) return;
      this.$nextTick(() => {
        this.$refs["chat"].scrollTop = this.$refs["chat"].scrollHeight;
        setTimeout(() => {
          this.$refs["chat"].scrollTop = this.$refs["chat"].scrollHeight;
        }, 1000);
      });
      // }, 200);
    }
  },
  beforeDestroy() {
    if (this.imagePreview) this.imagePreview.close();
  }
};
</script>

<style lang="less" scoped>
@import url("./../../../styles/common/base.less");
@footHeight: 40px;
#group-discussion {
  width: 100%;
  min-height: calc(100% - 44px);
  margin-bottom: 40px;
  background: #ebf0ef;

  .summary {
    width: 93vw;
    height: 45px;
    padding: 0 3.5vw;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;

    font-size: @font-sub-title;
    font-family: MicrosoftYaHei;
    color: rgba(153, 153, 153, 1);
    line-height: 45px;

    span {
      font-size: @font-title * 1.3;
      font-family: DINPro-Regular;
      color: rgba(240, 122, 35, 1);
    }
  }

  .chat {
    height: calc(100vh - 44px - 45px - @footHeight);
    overflow-y: auto;
  }

  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: @footHeight;
    background: rgba(255, 255, 255, 1);
    overflow: hidden;
    text-align: left;
    .footer_text {
      width: 75vw;
      margin: 0 2.5vw;

      float: left;
      text-align: left;
      border: none;

      input {
        background: rgba(235, 240, 239, 1);
        font-size: @font-sub-title;
        color: rgba(153, 153, 153, 1);
        width: 100%;
        height: @footHeight - 10px;
        line-height: @footHeight - 10px;
        margin-top: 5px;
        padding-left: 2vw;
      }
    }
    .icons {
      width: 18vw;
      float: left;
      padding-left: 2vw;
      .iconfont {
        font-size: @font-title * 1.5;
        line-height: @footHeight;
      }
      .van-button {
        line-height: @footHeight - 10px;
        height: @footHeight - 10px;
        font-size: @font-sub-title;
        padding: 0 2vw;
        margin: 5px auto;
      }
    }
  }
}
</style>



